.header{font-size:13px;line-height:1.1875rem;background:linear-gradient(359deg, rgb(235, 184, 215) 0%, rgb(255, 207, 237) 43%, rgb(255, 237, 248) 79%, rgb(255, 237, 248) 100%);border-radius:5px 5px 0 0;border:1px solid #ffbbe0;display:flex;align-items:center;height:25px;color:#525252;padding:0 5px;cursor:pointer;justify-content:space-between}.header img{height:70%;margin-right:5px}@media(max-width: 768px){.header{width:100%}}.title{display:flex;height:100%;align-items:center}@media(max-width: 768px){.title span{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;width:85%}}.buttons{display:flex;align-items:center;gap:2px}.buttons button{border-radius:50%;height:20px;width:20px;cursor:pointer}.minimize{background:#ffca37;background:linear-gradient(0deg, rgb(255, 202, 55) 0%, rgb(255, 245, 40) 71%, rgb(255, 250, 149) 100%);border:1px solid #a3832c}.minimize:hover{background:linear-gradient(0deg, rgb(255, 250, 149) 0%, rgb(255, 245, 40) 21%, rgb(255, 202, 55) 100%)}.maximize{background:#80ff37;background:linear-gradient(0deg, rgb(94, 185, 41) 0%, rgb(142, 226, 94) 51%, rgb(176, 233, 144) 100%);border:1px solid #5d9b39}.maximize:hover{background:linear-gradient(0deg, rgb(176, 233, 144) 0%, rgb(142, 226, 94) 51%, rgb(94, 185, 41) 100%)}.close{background:#f81f1f;background:linear-gradient(0deg, rgb(248, 31, 31) 0%, rgb(235, 79, 79) 61%, rgb(248, 163, 163) 100%);border:1px solid #8a1515}.close:hover{background:linear-gradient(0deg, rgb(248, 163, 163) 0%, rgb(235, 79, 79) 41%, rgb(248, 31, 31) 100%)}
